import React, { useState, useEffect } from 'react';
import './App.css';
import './styles/MainWhite.css';
import Cookies from 'js-cookie'

import { dummyPresets, pages } from './Utils';

import goIcon from "./assets/go.png"
import presetsIcon from "./assets/PRESETS.png"
import addPresetIcon from "./assets/ADD PRESET.png"
import cancelIcon from "./assets/CANCEL.png"
import saveIcon from "./assets/SAVE.png"
import stopIcon from "./assets/STOP.png"

import Splash from './pages/Splash';
import Presets from './pages/Presets';
import CreatePreset from './pages/CreatePreset';
import Set from './pages/Set';

import Links from "./Links"

function App() {

  const [currentPage, setCurrentPage] = useState(pages.HOME);
  const [presets, setPresets] = useState(dummyPresets);
  const [presetDraft, setPresetDraft] = useState({});

  useEffect(() => {
    getCurrentPage()
    if (Cookies.get("presets")) {
      setPresets(JSON.parse(Cookies.get("presets")))
    }
  }, []);



  function onGoPressed() {
    setCurrentPage(pages.GO)
  }

  function onPresetsPressed() {
    setCurrentPage(pages.PRESETS)
  }

  function onCreatePresetPressed() {
    setCurrentPage(pages.CREATE_PRESET)
  }


  function onCreatePresetCancelPressed() {
    setCurrentPage(pages.PRESETS)
  }


  function onCreatePresetSavePressed() {
    var _presetDraft = presetDraft
    if (!_presetDraft.name) {
      return
    }
    var _presets = [...presets]
    _presets.push(_presetDraft)
    setPresets(_presets)
    setCurrentPage(pages.PRESETS)
    saveCookie(_presets)
  }

  function saveCookie(_presets) {
    Cookies.set("presets", JSON.stringify(_presets), { expires: 365 })
  }


  function onGoStopPressed() {
    setCurrentPage(pages.PRESETS)
  }

  function getCurrentPage() {
    if (currentPage === pages.PRESETS) {
      return (
        <div>
          <div className="nav">
            <img className="nav-button" src={goIcon} onClick={onGoPressed} />
            <img className="nav-button" src={addPresetIcon} onClick={onCreatePresetPressed} />
          </div>
          <Presets presets={presets} setPresets={setPresets} saveCookie={saveCookie} />
        </div>
      )
    }

    if (currentPage === pages.CREATE_PRESET) {
      return (
        <div>
          <div className="nav">
            <img className="nav-button" src={cancelIcon} onClick={onCreatePresetCancelPressed} />
            <img className="nav-button" src={saveIcon} onClick={onCreatePresetSavePressed} />
          </div>
          <CreatePreset presets={presets} setPresetDraft={setPresetDraft} />
        </div>
      )
    }

    if (currentPage === pages.GO) {
      return (
        <div>
          <img className="nav-button" src={stopIcon} onClick={onGoStopPressed} />
          <div className="current-set">
            <Set presets={presets}/>
          </div>
        </div>
      )
    }

    else {
      return (
        <div>
          <div className="nav">
            <img className="nav-button" src={goIcon} onClick={onGoPressed} />
            <img className="nav-button" src={presetsIcon} onClick={onPresetsPressed} />
          </div>
          <Splash />
        </div>
      )
      return
    }
  }

  return (
    <div className="App">
      {getCurrentPage()}
      <Links />
    </div>
  );
}

export default App;
