import React from 'react'

import proIcon from "./assets/PRO.png"
import shareIcon from "./assets/SHARE.png"
import learnIcon from "./assets/LEARN.png"
import resetIcon from "./assets/RESET.png"
import kitIcon from "./assets/KIT.png"


function Links() {
    return (
        <div className="footer-links">
            <img className="footer-link" src={proIcon} />
            <img className="footer-link" src={shareIcon} />
            <img className="footer-link" src={learnIcon} />
            <img className="footer-link" src={resetIcon} />
            <img className="footer-link" src={kitIcon} onClick={() => window.open("http://12react.com/12react-kit.html", "_blank")} />
        </div>
    )
}

export default Links
