import React from 'react'
import chita from "../assets/chita.gif"


function Splash() {
    return (
        <div className="splash-div">
            <div className="logo">
                Want To React?
                <br />
                <span className="logo big">
                    12REACT
                </span>
                <br />
                <span className="logo small">
                    The Best U Can B
                </span>
                <br />
                <img src={chita} className="logo-chita"/>
            </div>
        </div>
    )
}

export default Splash
