import React from 'react';

import editIcon from "../assets/EDIT.png"
import deletIcon from "../assets/DELETE.png"


function Presets({ presets, setPresets, saveCookie }) {

    function deletePreset(item) {
        console.log(presets.findIndex((p) => p === item));
        var _presets = [... presets]
        _presets = _presets.filter((p) => p !== item)
        setPresets(_presets)
        saveCookie(_presets)
    }


    function renderTableData() {
        return presets.map((preset) => {
            const { queue, name, repeat } = preset //destructuring
            return (
                <tr key={name}>
                    <td >{queue}</td>
                    <td>{name}</td>
                    <td >
                        <input type="text" defaultValue={repeat} />
                    </td>
                    <td >
                        <img className="table-button" src={editIcon} />
                    </td>
                    <td >
                        <img className="table-button" src={deletIcon} onClick={() => deletePreset(preset)} />
                    </td>
                </tr>
            )
        })
    }


    return (
        <div className='presets-div'>
            <table>
                <thead>
                    <tr>
                        <th style={{ width: "10%" }}>queue</th>
                        <th>name</th>
                        <th style={{ width: "10%" }}>repeat</th>
                        <th style={{ width: "10%" }}>edit</th>
                        <th style={{ width: "10%" }}>delete</th>
                    </tr>
                </thead>
                <tbody>
                    {renderTableData()}
                </tbody>
            </table>
        </div>
    )
}

export default Presets
